.tui-pagination {
  margin-top: 50px;
  height: 40px;
  text-align: center;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  padding: 12px 0 12px;
  color: var(--black);
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination .tui-page-btn:hover {
  background-color: var(--text-orange);
  border-radius: 5px;
  transition: 0.5s;
  transform: scale(1.133);
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  background-color: var(--text-orange);
  color: var(--white);
  border-radius: 5px;
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: var(--text-orange);
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 40px;
  padding: 0;
  font-size: 0;
  line-height: 35px;
  white-space: nowrap;
  font-weight: normal;
  background-color: var(--color-footer);
  border-radius: 5px;
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;

  border-radius: 5px;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
  color: var(--black);
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 10px;
  background-color: var(--color-footer);
  border-radius: 5px;
}

.tui-pagination .tui-next {
  margin-left: 10px;
  background-color: var(--color-footer);
  border-radius: 5px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled {
  display: none;
  background: f7f7f7;
}

.tui-pagination .tui-is-disabled:hover {
  background-color: grey;
  cursor: default;
  border-radius: 5px;
  width: 40px;
  height: 40px;
  justify-content: center;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  display: none;
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  display: none;
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  display: none;
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  display: none;
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: var(--text-orange);
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}
