html {
  transition: background-color 0.3s;
}

body {
  font-family: var(--main-font);
  font-style: normal;
  font-weight: 500;
  color: var(--black);
  background: var(--color-background);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  // // border-radius: 5px;
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.link {
  text-decoration: none;
}

